a,
html,
body,
div,
p,
hr,
h1,
h2,
h3,
h4,
h5,
h6,
button,
img,
ol,
ul,
li,
img {
    margin: 0;
    padding: 0;
    font-weight: inherit;
    font-style: inherit;
    text-decoration: none;
    list-style: none;
}

a {
    all: unset;
}

a:hover {
    cursor: pointer;
}

input[type="text"]{
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
    margin: 0;
}

.Wrapper{
    max-width: 1920px;
    margin: 0 auto;

    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none;
}